.footer{
    background-color: #333; 
    padding: 10px;   
    width: 100%;
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    display: flex;
    flex-direction: column;
    padding: 1rem;
    max-width: 100%;
    height: 100%;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{
   line-height: 30px;
}

.right{
    display: flex;
    flex-direction: column;
    padding: 1rem;
    max-width: 100%;
    height: 100%;
}

.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1.2rem;
}

@media screen and (max-width: 640px){
    .footer-container{
        grid-template-columns: 1fr;
    }
    
}